.loader-wrap{
    position: relative;
    top:200px;
    left: 50%;
    border: 4px solid rgba(0, 0,0 , .1);
    width:80px;
    height: 80px;
    border-radius: 50%;
    border-left-color: orange;
    animation: spin 1s linear infinite;
}

@keyframes spin{
    0%{
        transform: rotate(0deg);
    }
    100%{
        transform: rotate(360deg);
    }
}